import React from 'react'
import '../App.css'
import {Link} from 'react-router-dom'

const CityCard = (props) => (
    <div className="col-4">
        <div className="card bg-light mb-3">
        <div className="card-body">
            <img src={props.city.country_flag_url} alt="Indonesia" width='20%'/>
            <h3 className="card-title city-name">{props.city.name}</h3>
            <p>{props.city.country_name}</p>
            <Link to={`/city/${props.city.id}`} className="card-text">
            See Tempongan in {props.city.name}
            </Link>
        </div>
        </div>
    </div>
)
export default CityCard