import React from 'react'
import '../pages/City.css'

const SearchKeyword = props => (
    <>
    <h5>Keyword</h5>
        <div className="card card-keyword">
            <div className="card-body">
                <div className="form-row">
                    <div className="col-9">
                        <input 
                            className="form-control input-keyword"
                            type="text"
                            placeholder="Type keyword i.e : restaurant's name, location, cuisine, etc."
                            value={props.keyword}
                            onChange={props.changeKeywordHandler}
                        />
                    </div>
                    <div className="col-3">
                        <button onClick={props.onClickAddToCriteria} className="btn btn-primary btn-keyword" type="button">
                            Add to Criteria
                        </button>
                    </div>
                </div>
            </div>
        </div>
    </>
)

export default SearchKeyword