import React from 'react'
import mainImage from "../assets/images/sego.jpg"
import '../App.css'
const ImageAndWelcome = () => (
    <>
    <div className="container-fluid">
        <div className="row">
            <div className="col">
                <img src={mainImage} className="img-responsive" width="100%" alt="aneka-makanan"/>
            </div>
        </div>
        <div className="row row-welcome">
            <div className="col">
                <div className="card text-white bg-success mb-3 text-center">
                    <div className="card-header text-head"><p>Welcome to Tempongan</p></div>
                    <div className="card-body">
                        <h4 className="card-title">The Easiest Way to mencari makanan banyuwangi in another city</h4>
                        <p className="card-text">You can grab information about makanan banyuwangi yang berada di cafe dan resto hanya sekali klik saja.</p>
                        <p className="card-text">Start by choosing the fitur dengan pilihan nama resto, or search nama kotanya.</p>
                    </div>
                </div>
            </div>
        </div>
    </div>
    </>
)
export default ImageAndWelcome