import React from 'react'
import '../App.css'
const SearchCity = (props) => (
    <div className="row" style={{marginBottom: 30}}>
        <div className="col-12">
            <h3>Search City</h3>
            <div className="card">
                <div className="card-body">
                    <div className="form-row">
                        <div className="col-11">
                            <input
                            className="form-control input-search"
                            type="text"
                            placeholder="Type keyword or city name"
                            value={props.value}
                            onChange={props.onChange}
                            />
                        </div>
                        <div className="col-1 p-auto m-auto">
                            <button className="btn btn-primary btn-search" type="button" onClick={props.onClickSearch}>
                            <i className="fa fa-search">&nbsp;<p className="text-search" style={{display:"inline"}}>Search</p></i></button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
)

export default SearchCity